

































import { Component, Prop, Vue, Ref } from "vue-property-decorator";
import { Icon } from "@iconify/vue2";
import { Work } from "@/models/Work";
import ImageGallery from "@/components/ImageGallery.vue";

@Component({
  components: {
    Icon,
    ImageGallery,
  },
})
export default class TitleText extends Vue {
  @Prop({ required: true, type: Work })
  readonly work: Work;

  @Ref("imageGallery")
  private readonly imageGallery: any;

  private overlay = false;

  private openGallery() {
    this.overlay = true;
    // Immediately focus on image gallery when it is open
    this.imageGallery.$el.focus();
    document.documentElement.style.overflow = "hidden";
  }

  private closeGallery() {
    this.overlay = false;
    // Immediately unfocus on image gallery when it is open
    this.imageGallery.$el.blur();
    document.documentElement.style.overflow = "auto";
  }
}
